import React from 'react'

import Layout from '../components/layout/layout'
import PageContent from '../components/layout/pageContent';
import Document from '../components/content/documentComponent';
import items_en from '../data/api/data--document_component--en';
import items_fr from '../data/api/data--document_component--fr';

const DocumentPage = ({pageContext}) => {
    let items = pageContext.lang === 'en' ? items_en : items_fr;
    return (
        <Layout context={pageContext}>
            <PageContent context={pageContext}/>
            <Document items={items} lang={pageContext.lang}/>
        </Layout>
    )
};

export default DocumentPage
