import React from 'react';
import PropTypes from 'prop-types';
import YearCarousel from '../layout/yearCarousel';
import ArrayHandler from '../../utils/array-handler';
import '../../styles/animate.min.css';
import _ from 'lodash';
import {Container, Row, Col} from 'reactstrap';
import Time from '../layout/time';
import T from '../layout/translation';

export default class Document extends React.Component {
    constructor() {
        super();
        this.state = {
            currentYear: 0
        }
    }
    static propTypes = {
        items: PropTypes.object.isRequired,
        lang: PropTypes.string.isRequired
    };

    selectYear = (year) => {
        this.setState({
            currentYear: year
        });
    };

    render() {
        let types = _.keys(this.props.items).sort().reverse();
        return (
            <Container>
                <Row>
                    <Col lg={{ size: 6, offset: 3 }}  md={{ size: 8, offset: 2 }} xs="12" className="financial-publication">
                        {types.map(type => (
                            <div key={type} className="financial-publication-container">
                                <div>
                                    <h4>{type}</h4>
                                    <div>
                                        {ArrayHandler.sortColumns(this.props.items[type], ['title'], ['asc']).map(node => (
                                            <Row key={node.nid} className="financial-publication-item">
                                                <Time lang={this.props.lang} date={node.date}/>
                                                <Col lg="9"  md="8" xs="8">
                                                    <h5>{node.title}</h5>
                                                    <a href={node.file} target="_blank">
                                                        <T message="Download"/>
                                                    </a>
                                                </Col>
                                            </Row>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        ))}
                    </Col>
                </Row>
            </Container>
        )
    }
}
